.ErrorElem {
  margin: 0;
  padding: 28px;
  border-radius: 5px;
  background: #FFF;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 35px;
  align-items: center;
  justify-content: center;
}

.Error__wrapper {
  padding: 27px;
  margin-bottom: auto;
  height: calc(100vh - 250px);
}